<template>
  <div class="hello">
        <p class="titles">拼单</p>
        <div class="flex">
        <div style="margin-bottom:20px;margin-right:100px">
             <Alert style="width:320px" type="error" show-icon>
                 已完结
                 <Icon type="md-checkmark"slot="icon" />
             </Alert>
               <Card style="width:320px">
                <div style="text-align:center">
                    <h3>125单</h3>
                </div>
            </Card>
        </div>
        <div>
             <Alert style="width:320px" type="info" show-icon>拼单中
                  <Icon type="ios-globe" slot="icon" />
             </Alert>
             <Card style="width:320px">
                <div style="text-align:center">
                    <h3>35单</h3>
                </div>
            </Card>

        </div>
            
        </div>
        <p class="titles">交易</p>


          <div class="flex">
        <div style="margin-bottom:20px;margin-right:100px">
             <Alert >今日交易量
                 
             </Alert>
             <Card style="width:320px">
                <div class="flex sbts" style="text-align:center">
                    <h3 >订单数</h3>
                    <h3>金额数</h3>
                </div>
                <div class="flex sbts sub" >
                    <h3 >35单</h3>
                    <h3>￥1580</h3>
                </div>
            </Card>

        </div>
        <div>
             <Alert  >近一个月交易
                
             </Alert>
              <Card style="width:320px">
                <div class="flex sbts" style="text-align:center">
                    <h3 >订单数</h3>
                    <h3>金额数</h3>
                </div>
                <div class="flex sbts sub" >
                    <h3 >35单</h3>
                    <h3>￥1580</h3>
                </div>
            </Card>

        </div>
        </div>
  </div>
</template>

<script>
export default {
  name: 'HelloWorld',
  data () {
    return {
      msg: 'Welcome to Your Vue.js App'
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.titles{
    margin: 20px auto;
}
.sub{
    font-size: 13px;
    color: rgb(140, 200, 255)
}
</style>



